<script setup>
const props = defineProps({
	board: Object,
	invalid: Boolean
});
function css(cell) {
	return `${cell.status} ${cell.animate && "spring"}`;
}
</script>
<template>
	<div class="board" :class="{ shake: invalid }">
		<div class="row" v-for="row in props.board">
			<div class="cell" :class="css(cell)" v-for="cell in row">{{ cell.value }}</div>
		</div>
	</div>
</template>

<style>
.board {
	@apply aspect-square w-full font-sans gap-1 flex flex-col;
}
.row {
	@apply flex flex-row gap-1;
}
.cell {
	@apply aspect-square w-full shadow-sm rounded-sm bg-slate-300 flex justify-center items-center text-4xl transition-colors;
}
.cell.right-position {
	@apply bg-green-500;
}
.cell.letter-contained {
	@apply bg-orange-500;
}
.cell.wrong-letter {
	@apply bg-slate-500;
}
.board.shake {
	@apply animate-shake;
}
.cell.spring {
	animation: spring 300ms linear;
}
@keyframes spring {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(0.9);
	}
	100% {
		transform: scale(1);
	}
}
</style>