<script setup>
import { reactive } from 'vue';
import { generateCells, generateWord, currentRowIsWord } from './helpers';
import Board from './components/Board.vue';
import Restart from './components/Restart.vue';
import Select from './components/Select.vue';
import Navbar from './components/Navbar.vue';
import Keyboard from './components/Keyboard.vue';

const game = reactive({
	guessWord: generateWord(5),
	board: generateCells(5),
	squareSize: 5,
	won: false,
	end: false,
	correctLetters: [],
	partlyCorrectLetters: [],
	incorrectLetters: [],
	invalidInput: false,
});
let currentRow = 0;
let currentColumn = 0;

console.log(game.guessWord);

function addLetter(letter) {
	if (currentColumn < game.squareSize && !game.end) {
		game.board[currentRow][currentColumn].value = letter;
		currentColumn++;
	}
}

function deleteLetter() {
	if (currentColumn > 0 && !game.end) {
		currentColumn--;
		game.board[currentRow][currentColumn].value = "";
	}
}

function addToUniqueArray(array, item) {
	return [...new Set([...array, item])]
}

function commitRow() {
	game.invalidInput = false;
	if (currentColumn !== game.squareSize) return setTimeout(() => game.invalidInput = true); // row still partly empty
	if (currentRow === game.squareSize) return; // all rows full

	if (!currentRowIsWord(game.board[currentRow], game.squareSize)) {
		return setTimeout(() => game.invalidInput = true); // implement toast
	}

	let allLettersRightlyPositioned = true;
	let row = game.board[currentRow];
	let wronglyPositionedLetters = game.guessWord.split("").filter((letter, i) => {
		let cellValue = row[i].value.toLowerCase();
		return cellValue !== letter;
	}); // filter out all letters, which are rightly positioned

	row.forEach((cell, i) => { // then check the remaining ones if they are present
		let cellValue = cell.value.toLowerCase();
		// rightly positioned letters
		if (game.guessWord[i] === cellValue) {
			game.correctLetters = addToUniqueArray(game.correctLetters, cellValue);
			return cell.status = "right-position";
		}

		// contained letters, but wrongly positioned
		allLettersRightlyPositioned = false;
		if (wronglyPositionedLetters.includes(cellValue)) { // avoid marking double letters as contained
			wronglyPositionedLetters.splice(wronglyPositionedLetters.indexOf(cellValue), 1);
			game.partlyCorrectLetters = addToUniqueArray(game.partlyCorrectLetters, cellValue);
			return cell.status = "letter-contained";
		}

		// wrong letter
		game.incorrectLetters = addToUniqueArray(game.incorrectLetters, cellValue);
		cell.status = "wrong-letter";
	});
	currentColumn = 0;
	currentRow++;
	if (allLettersRightlyPositioned) { // if all letters are right set game to won
		game.won = true;
		game.end = true;
		row.forEach((cell, i) => cell.animate = true);
	}
	if (currentRow === game.squareSize) game.end = true; // all rows full -> game ends
}

function keypress(e) {
	const key = e.key.toUpperCase();

	if (key.match(/^\w$/)) // word character
		return addLetter(key);

	if (key === "ENTER")
		return commitRow();

	if (key === "BACKSPACE")
		return deleteLetter();
}
window.addEventListener("keydown", keypress);
function restart(e) {
	game.end = false;
	game.won = false;
	game.board = generateCells(game.squareSize);
	game.guessWord = generateWord(game.squareSize);
	game.correctLetters = [];
	game.partlyCorrectLetters = [];
	game.incorrectLetters = [];
	currentRow = 0;
	currentColumn = 0;
}
</script>

<template>
	<Navbar />
	<div class="wrapper">
		<Select v-model="game.squareSize" @change="restart" />
		<Board :board="game.board" :invalid="game.invalidInput" />
	</div>
	<Keyboard
		:correctLetters="game.correctLetters"
		:partlyCorrectLetters="game.partlyCorrectLetters"
		:incorrectLetters="game.incorrectLetters"
		@keypressed="keypress({ key: $event })"
	/>
	<Restart @click="restart" :show="game.end" :won="game.won" />
</template>

<style>
.wrapper {
	@apply h-full w-full max-w-md px-4 font-sans mx-auto flex flex-col justify-center;
}
</style>
