<script setup>
import { ref } from 'vue';
import RestartSVG from './components/RestartSVG.vue';
const emit = defineEmits(["click"])
defineProps({
	won: Boolean,
	show: Boolean
});
const animate = ref(false);
function handleClick(e) {
	animate.value = true;
	setTimeout(() => emit("click", e), 400);
}
</script>

<template>
	<Teleport to="body">
		<Transition>
			<div class="overlay" v-if="show">
				<h2 class="winning-text">{{ won ? "You Won" : "You Lost" }}</h2>
				<button class="restart-button" :class="{ rotate: animate }" @click="handleClick">
					<RestartSVG />
				</button>
			</div>
		</Transition>
	</Teleport>
</template>

<style>
.winning-text {
	@apply uppercase text-4xl font-bold text-slate-100 tracking-[0.2rem] mb-9
		sm:text-6xl;
}
.overlay {
	@apply fixed top-0 left-0 bg-[rgba(0,0,0,0.5)] w-full h-full flex flex-col justify-center items-center;
}
svg {
	@apply w-full h-auto fill-slate-100;
}
.restart-button {
	@apply w-14 sm:w-20;
}
.restart-button:not(.rotate) {
	@apply can-hover:hover:-rotate-12 transition-transform;
}
.rotate {
	@apply animate-rotate;
}

.v-enter-active,
.v-leave-active {
	transition: opacity 0.6s ease;
}
.v-enter-active {
	transition-delay: 0.2s;
}
.v-enter-to {
	opacity: 1;
}
.v-enter-from,
.v-leave-to {
	opacity: 0;
}
</style>
