import wordlist from "./wordlist.json";

function deepClone(obj) {
	if (structuredClone) return structuredClone(obj);
	return JSON.parse(JSON.stringify(obj));
}

export function generateCells(squareSize) {
	let cell = { value: "", status: "", animate: false }; // status = "" | "right-position" | "letter-contained"
	let row = Array.from({ length: squareSize }, _ => ({ ...cell }));
	let rows = Array.from({ length: squareSize }, _ => deepClone([...row]));
	return rows;
}

export function generateWord(squareSize) {
	const correctLengthList = wordlist[squareSize];
	let randomI = Math.floor(correctLengthList.length * Math.random());
	return correctLengthList[randomI];
}

export function currentRowIsWord(row, squareSize) {
	const rowAsString = row.reduce((prev, curr) => prev + curr.value, "").toLowerCase();
	return wordlist[squareSize].includes(rowAsString)
}