<script setup>
const props = defineProps({
	correctLetters: Array,
	partlyCorrectLetters: Array,
	incorrectLetters: Array
});
defineEmits(["keypressed"]);
import Delete from './components/Delete.vue';
import Enter from './components/Enter.vue';
import Key from "./components/Key.vue";
let keyboard = [
	[
		{ value: "Q" },
		{ value: "W" },
		{ value: "E" },
		{ value: "R" },
		{ value: "T" },
		{ value: "Y" },
		{ value: "U" },
		{ value: "I" },
		{ value: "O" },
		{ value: "P" },
	],
	[
		{ value: "A" },
		{ value: "S" },
		{ value: "D" },
		{ value: "F" },
		{ value: "G" },
		{ value: "H" },
		{ value: "J" },
		{ value: "K" },
		{ value: "L" },
		{ value: "Backspace", component: Delete },
	],
	[
		{ value: "Z" },
		{ value: "X" },
		{ value: "C" },
		{ value: "V" },
		{ value: "B" },
		{ value: "N" },
		{ value: "M" },
		{ value: "Enter", component: Enter },
	]
]
</script>

<template>
	<div class="center-wrapper">
		<div class="keyboard">
			<div class="row" v-for="row in keyboard">
				<Key
					v-for="key in row"
					:value="key.value"
					:correctLetters="props.correctLetters"
					:partlyCorrectLetters="props.partlyCorrectLetters"
					:incorrectLetters="props.incorrectLetters"
					@keypressed="$emit('keypressed', $event)"
				>
					<span v-if="!key.component">{{ key.value }}</span>
					<component v-else :is="key.component" class="fill-black" />
				</Key>
			</div>
		</div>
	</div>
</template>

<style>
.center-wrapper {
	@apply h-2/6 sm:h-3/6 bg-[#3d3d3d];
}
.keyboard {
	@apply h-full w-full max-w-md  flex flex-col gap-1 px-4 py-3 sm:py-7 mx-auto;
}
.row {
	@apply h-3/6 flex gap-1 justify-center;
}
</style>