<script setup>
import { ref, computed } from "vue";
const props = defineProps({
	value: String,
	correctLetters: Array,
	partlyCorrectLetters: Array,
	incorrectLetters: Array
});
defineEmits(["keypressed"])
const css = computed(() => {
	let css = "";
	let key = props.value.toLowerCase();
	if (props.correctLetters.includes(key)) css = "right"
	else if (props.partlyCorrectLetters.includes(key)) css = "partly-right"
	else if (props.incorrectLetters.includes(key)) css = "wrong"
	return css
});
</script>

<template>
	<button class="key" :class="css" @click="$emit('keypressed', value)">
		<slot></slot>
	</button>
</template>

<style>
.key {
	@apply h-full w-10 bg-slate-100 rounded-sm flex justify-center items-center hover:active:bg-slate-400 can-hover:hover:bg-slate-300;
}
.key.right {
	@apply bg-green-500;
}
.key.partly-right {
	@apply bg-orange-500;
}
.key.wrong {
	@apply bg-slate-500;
}
</style>